import './full-screen.css';

const fullScreenAnimation = document.createElement('div');
fullScreenAnimation.className = 'full-screen-animation';
const insideScreen = document.createElement('div');
insideScreen.className = 'full-screen-animation-inside';
fullScreenAnimation.appendChild(insideScreen);
const fullScreenExit = document.createElement('div');
fullScreenExit.className = 'full-screen-exit';
fullScreenExit.innerHTML = '<i class="fas fa-times"></i>';
fullScreenAnimation.appendChild(fullScreenExit);

/* Все функции из основного файла анимации */

const sliderFPS = document.querySelector('.fps-slider');

// eslint-disable-next-line consistent-return
function addInfoFrames() {
  if (document.querySelector('.full-screen-animation')) {
    const frames = [];
    const framesAll = document.querySelectorAll('.frame');
    for (let i = 0; i < framesAll.length; i += 1) {
      const framePixels = framesAll[i].children[4].toDataURL();
      frames.push(framePixels);
    }
    return frames;
  }
}

function draw(frame) {
  if (document.querySelector('.full-screen-animation')) {
    const preview = document.querySelector('.full-screen-animation-inside');
    preview.style.backgroundImage = `url(${frame})`;
    preview.style.backgroundSize = '100%';
    preview.style.backgroundRepeat = 'no-repeat';
  }
}

let count = 0;
let intervalID;
let frameRate;
// анимация всех фреймов с указанным фреймрейтом
function frameAnimation() {
  intervalID = setInterval(() => {
    if (document.querySelector('.full-screen-animation')) {
      const frames = addInfoFrames();
      const frame = frames[count % frames.length];
      draw(frame);
      count += 1;
    }
  }, 1000 / frameRate);
}

function animation() {
  if (document.querySelector('.full-screen-animation')) {
    frameRate = +sliderFPS.value;
    if (frameRate > 0) {
      clearInterval(intervalID);
      frameAnimation();
    } else {
      clearInterval(intervalID);
    }
  }
}

// full screen
export default function toggleFullScreen() {
  if (!document.querySelector('.full-screen-animation')) {
    document.body.appendChild(fullScreenAnimation);
    animation();
    document.querySelector('.full-screen-exit').onclick = () => {
      document.body.removeChild(document.querySelector('.full-screen-animation'));
    };
  } else {
    document.body.removeChild(document.querySelector('.full-screen-animation'));
    clearInterval(intervalID);
  }
}
